.popup__submit-button {
  display: block;
  border: 0;
  margin: 0;
  padding: 0;
  margin-top: 48px;
  width: 100%;
  height: 50px;
  background-color: #000000;
  border-radius: 2px;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.21;
  color: white;
  cursor: pointer;
  transition: opacity 0.5s linear;
}

@media screen and (max-width: 618px) {
  .popup__submit-button {
    margin-top: 45px;
    font-size: 14px;
  }
}

.popup__submit-button:hover {
  opacity: 0.8;
}
