.profile__name {
  margin: 0;
  font-family: 'Inter', Arial, sans-serif;
  display: inline-block;
  max-width: calc(100% - 60px);
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  color: #ffffff;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 618px) {
  .profile__name {
    font-size: 27px;
    line-height: 1.22;
    text-align: center;
    max-width: 100%;
  }
}