.popup__form-item {
  display: block;
  padding: 0;
  padding-left: 10px;
  border: 0;
  height: 27px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
}

.popup__form-item:not(:first-of-type) {
  margin-top: 30px;
}
